/** ======================== Styles ======================================== */
.calendar-container {
  width: 350px;
  box-shadow: 0 15px 15px 0 rgba(16, 36, 94, 0.2);
}
.calendar-container .react-calendar__tile {
  position: relative;
  z-index: 0;
}
.calendar-container .react-calendar__tile:disabled,
.calendar-container .react-calendar__tile.booked {
  background-color: #ddd;
  color: rgba(0, 0, 0, 0.65);
}
.calendar-container .react-calendar__tile.start .triangle.late {
  border-bottom: 21px solid #ddd;
  border-right: 24.85714286px solid #ddd;
}
.calendar-container .react-calendar__tile.end .triangle.early {
  border-top: 21px solid #ddd;
  border-left: 24.85714286px solid #ddd;
}
.calendar-container .react-calendar__tile .triangle {
  z-index: -1;
  position: absolute;
  border-top: 21px solid transparent;
  border-left: 24.85714286px solid transparent;
  border-bottom: 21px solid transparent;
  border-right: 24.85714286px solid transparent;
}
.calendar-container .react-calendar__tile .triangle.early {
  top: 0;
  left: 0;
}
.calendar-container .react-calendar__tile .triangle.late {
  bottom: 0;
  right: 0;
}
.calendar-container .react-calendar--selectRange .react-calendar__tile--hover:enabled,
.calendar-container .react-calendar--selectRange .react-calendar__tile--active {
  background: #006edc;
  color: white;
}
.calendar-container .react-calendar__tile--active:enabled:focus:not(:hover) {
  background: #006edc;
}
.calendar-container .react-calendar__month-view__days__day--weekend:not(.react-calendar__tile--active) {
  color: rgba(0, 0, 0, 0.65);
}
